<template>
  <div
    class="drop-down-box"
    @mouseout="dropDown('out')"
    @mouseover="dropDown('over')"
  >
    <div class="drop-down-title">
      <div class="title">{{ menuTitle }}</div>
      <div class="arrow">▼</div>
    </div>
    <div class="drop-down-menu">
      <div class="menu">
        <div class="menu-item" v-for="item in menuItem" :key="item">
          {{ item }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Drop-Down-Menu-Component",

  props: {
    menuTitle: {
      type: String,
      default: "Menu",
    },

    menuItem: {
      type: Array,
      default: () => ["Menu Item 1", "Menu Item 2", "Menu Item 3"],
    },
  },

  methods: {
    dropDown(status) {
      if (status === "over") {
        this.$el.querySelector(".drop-down-menu").style.display = "block";
        this.$el.querySelector(".arrow").innerHTML = "▲";
        // change background color of menu title
        this.$el.querySelector(".drop-down-title").style.backgroundColor =
          "#13b955";
        this.$el.querySelector(".drop-down-title").style.color = "#fff";
      } else {
        this.$el.querySelector(".drop-down-menu").style.display = "none";
        this.$el.querySelector(".arrow").innerHTML = "▼";
        // change background color of menu title
        this.$el.querySelector(".drop-down-title").style.backgroundColor =
          "#fff";
        this.$el.querySelector(".drop-down-title").style.color = "#000";
      }
    },
  },
};
</script>

<style scoped>
.drop-down-box {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border-bottom: 1px dashed rgb(168, 168, 168);
}

.drop-down-title {
  width: 100%;
  height: 100%;
  padding: 10px 0;
  display: flex;
  font-weight: bold;
  align-items: center;
  justify-content: center;
}

.arrow {
  font-size: 13px;
  margin-left: 10px;
}

.drop-down-menu {
  top: 100%;
  width: 100%;
  height: 100%;
  display: none;
  position: absolute;
  align-items: center;
  justify-content: center;
}

.menu-item {
  width: 100%;
  height: 100%;
  cursor: pointer;
  display: flex;
  padding: 10px 0;
  position: relative;
  font-size: 13px;
  background: #fff;
  align-items: center;
  justify-content: center;
}
.menu-item:hover {
  background-color: #e2dfdf;
}
</style>
